import {bedug_mode} from "./store-redux";
import {apiCommon} from "../components/api/apiLocalStorage";


const SET_THEME = "myApp/dark-light-reducer/SET_THEME"; //  

export let setTheme = (themeUpdate) => { //   
  return {type: SET_THEME, themeUpdate }
};

let initialState = { //   
  themeBLL: "light", //   bll  
}

let themeReducer = (state = initialState, action) => {//  
  let stateCopy; //      
  switch (action.type) {
    case SET_THEME: //   
      stateCopy = {
        ...state, //   
        themeBLL: action.themeUpdate, //    BLL
      }
      if (bedug_mode) {console.log("theme-reducer.js, SET_THEME: ", state, stateCopy)} // 
      return stateCopy; //     
    default:
      return state; //     
  }
}

export let setThemeThunkCreator = (theme1) => {//    LocalStorage
  let setThemeThunk = async (dispatch) => { //     LocalStorage
    const response1 = await apiCommon.putTheme1(theme1)  //    localStorage
    if (response1) {
      if (bedug_mode) {console.log("theme-reducer.js, setThemeThunkCreator dispatch(setTheme()) ->SET_THEME" )} // 
      dispatch(setTheme(response1))  //   localStorage    store
    }

  }
  return setThemeThunk
}
export let getThemeThunkCreator = () => {//    LocalStorage
  let getThemeThunk = async (dispatch) => { //     LocalStorage
    const response1 = await apiCommon.getTheme1()  //    localStorage
    if (response1) {
      if (bedug_mode) {console.log("theme-reducer.js, getThemeThunkCreator dispatch(setTheme()) ->SET_THEME" )} // 
      dispatch(setTheme(response1))  //   localStorage    store
    }

  }
  return getThemeThunk
}


export default themeReducer;










