import React from 'react';
import Header from "./HeaderBS";
import {connect} from "react-redux";
import {deleteLoginThunkCreator} from "../../redux/auth-reducer";
import {getProfileThunkCreator} from "../../redux/profile-reducer";
import {getInfoModeThunkCreator, setInfoMode, setInfoModeThunkCreator} from "../../redux/app-reducer";
import ErrorBoundary from "../common/ErrorBoundary/ErrorBoundary";
import {compose} from "redux";
import {getThemeThunkCreator, setThemeThunkCreator} from "../../redux/theme-reducer";


class HeaderContainer extends React.Component {
    componentDidMount() {
        this.props.getInfoModeThunkCreator() //   localStorage    
        this.props.getThemeThunkCreator() //    localStorage
    }

    setTheme1 = () => {
        const theme1 = this.props.theme1==="light"?"dark":"light" //    
        this.props.setThemeThunkCreator(theme1) //   
    }

    deleteLogin = () => {
        this.props.deleteLoginThunkCreator()//   
    }
    switchInfo = () => {
        let info_modeLocal = !this.props.info_mode //  infoMode      localStorage
        this.props.setInfoModeThunkCreator(info_modeLocal) //  infoMode  localStorage

    }
    goToMyPage = () => {
        getProfileThunkCreator(this.props.myId); //    ID
    }

    render() {
        return <ErrorBoundary> {/*   Header*/}
            <Header
                {...this.props}
                deleteLogin={this.deleteLogin}
                switchInfo={this.switchInfo}
                goToMyPage={this.goToMyPage}
                setTheme1={this.setTheme1} //  1
            /> {/*  */}
        </ErrorBoundary>
    }
}

let mapStateToProps = (state) => {
    return {
        myLogin: state.auth.myLogin,
        myId: state.auth.myId,
        isAuth: state.auth.isAuth,
        myProfile: state.auth.myProfile,
        info_mode: state.app.info_mode,
        theme1: state.theme.themeBLL,
    }
}
export default compose(
    connect(mapStateToProps,
        {
            getProfileThunkCreator,
            deleteLoginThunkCreator,
            setInfoMode,
            setInfoModeThunkCreator,
            getInfoModeThunkCreator,
            setThemeThunkCreator,
            getThemeThunkCreator
        }),

)(HeaderContainer)




